import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor(private http: HttpClient) { }

  getAllItems() {
    return this.http.get('/api/cart', { responseType: 'json'});
  }
  deleteItem(id) {
    return this.http.delete(`/api/cart/${id}`, { responseType: 'json'});
  }
  editItem(newItem, id) {
    return this.http.put(`/api/cart/${id}`, { newItem }, { responseType: 'json'});
  }

}
